.stages-block {
	&__title {
		display: block;
		margin-bottom: 90px;
		text-align: center;
		font-size: 36px;
		font-weight: 700;
		line-height: 1.278;
		color: #333;
	}
	&__card-icon {
		display: block;
		margin-left: -14px;
		margin-bottom: 35px;
	}
	&__card-icon svg {
		display: block;
		margin: 0 auto;
	}
	&__card-inner {
		display: flex;
		flex-direction: row;
	}
	&__card-num {
		position: relative;
		top: -14px;
		display: inline-block;
		font-size: 36px;
		line-height: 1.194;
		font-weight: 700;
		color: rgba(130, 130, 130, 0.3);
	}
	&__card-title {
		display: inline-block;
		width: 100%;
		max-width: 132px;
		font-size: 18px;
		padding-left: 18px;
		line-height: 1.167;
		font-weight: 600;
	}
}